<template>
  <div class="containers" ref="containers">
    <div id="js-canvas" class="canvas" ref="canvas"></div>
    <div id="js-properties-panel" class="panel"></div>
    <ul class="buttons">
      <li></li>
      <li>
        <a ref="saveDiagram" href="javascript:" title="download BPMN diagram">BPMN diagram</a>
      </li>
      <li>
        <a ref="saveSvg" href="javascript:" title="download as SVG image">SVG image</a>
      </li>
      <el-upload :show-file-list="false" action="" :auto-upload="false" :on-change="changeFile" class="import">
        <li>
          <a slot="trigger" ref="import" href="javascript:" title="import BPMN diagram"> BPMN diagram</a>
        </li>
      </el-upload>
    </ul>
  </div>
</template>

<script>
  // Modeler
  import BpmnModeler from 'bpmn-js/lib/Modeler'
  import propertiesPanelModule from 'bpmn-js-properties-panel'
  import propertiesProviderModule from 'bpmn-js-properties-panel/lib/provider/activiti'
  import activitiModdleDescriptor from 'activiti-bpmn-moddle/resources/activiti'
  //  bpmn-js-examples-master ===> i18n
  import customTranslate from '../i18n/translate';

  export default {
    data () {
      return {
        bpmnModeler: null,
        containers: null,
        canvas: null,
        xmlStr: null,
        customTranslateModule: {
          translate: [ 'value', customTranslate ]
        }
      }
    },
    methods:{
      openDiagram(xml){
        this.bpmnModeler.importXML(xml, function(err) {
          if (err) {
            // container
            //     .removeClass('with-diagram')
            //     .addClass('with-error');
            console.error(err);
          } else {
            // container
            //   .removeClass('with-error')
            //   .addClass('with-diagram');
          }
        });
      },
      // bpmnbpmn
      createNewDiagram(){
        /*let bpmnXmlStr = `<?xml version="1.0" encoding="UTF-8"?>
          <definitions xmlns="http://www.omg.org/spec/BPMN/20100524/MODEL" xmlns:bpmndi="http://www.omg.org/spec/BPMN/20100524/DI" xmlns:omgdc="http://www.omg.org/spec/DD/20100524/DC" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" id="sid-38422fae-e03e-43a3-bef4-bd33b32041b2" targetNamespace="http://bpmn.io/bpmn" exporter="bpmn-js (https://demo.bpmn.io)" exporterVersion="6.0.4">
            <process id="Process_1" isExecutable="true">
              <startEvent id="StartEvent_1acpsqt" />
            </process>
            <bpmndi:BPMNDiagram id="BpmnDiagram_1">
              <bpmndi:BPMNPlane id="BpmnPlane_1" bpmnElement="Process_1">
                <bpmndi:BPMNShape id="StartEvent_1acpsqt_di" bpmnElement="StartEvent_1acpsqt">
                  <omgdc:Bounds x="152" y="82" width="36" height="36" />
                </bpmndi:BPMNShape>
              </bpmndi:BPMNPlane>
            </bpmndi:BPMNDiagram>
          </definitions>`
        this.openDiagram(bpmnXmlStr) */

        var diagramUrl = 'https://cdn.staticaly.com/gh/bpmn-io/bpmn-js-examples/dfceecba/starter/diagram.bpmn';
        // var diagramUrl = 'http://localhost:8080/newDiagram.bpmn';
        this.$http.get(diagramUrl)
                  // openDiagram
                  .then((res)=>{
            //console.log(res.data)
            this.openDiagram(res.data)
        })
        .catch((err)=>{
            console.log(err)
        })
      },
      // SVG,done
      saveSVG (done) {
        // donebpmnsaveSVG
        this.bpmnModeler.saveSVG(done)
      },
      // SVG,done
      saveDiagram (done) {
        // donebpmnsaveXML
        this.bpmnModeler.saveXML({ format: true }, function (err, xml) {
          done(err, xml)
        })
      },
      // dataxml
      setEncoded (link, name, data) {
        // xmlURI
        const encodedData = encodeURIComponent(data)
        // xmlxml
        this.xmlStr = data
        // ,aclassNameahrefdownload
        if (data) {
          link.className = 'active'
          link.href = 'data:application/bpmn20-xml;charset=UTF-8,' + encodedData
          link.download = name
        }
      },
      // 
      changeFile(file){
        let _this = this
        let reads = new FileReader()
        let f = file.raw
        reads.readAsText(f, 'utf-8')
        reads.onload = function (e) {
          let newXml = e.target.result
          _this.bpmnModeler.importXML(newXml, function (err) {
            if (err) {
              console.error(err)
            }
          })
        }
      }
    },
    mounted() {
      // refcontainersdom
      this.containers = this.$refs.containers
      // refcanvasdom
      const canvas = this.$refs.canvas
      // 
      this.bpmnModeler = new BpmnModeler({
        container: canvas,
        // 
        propertiesPanel: {
          parent: '#js-properties-panel'
        },
        additionalModules: [
          // 
          propertiesProviderModule,
          // 
          propertiesPanelModule,
          // 
          this.$data.customTranslateModule
        ],
        moddleExtensions: {
          activiti: activitiModdleDescriptor
        }
      })

      // 
      let _this = this
      // adom
      const downloadLink = this.$refs.saveDiagram
      const downloadSvgLink = this.$refs.saveSvg
      // 
      this.bpmnModeler.on('commandStack.changed', function () {
        _this.saveSVG(function (err, svg) {
          _this.setEncoded(downloadSvgLink, 'diagram.svg', err ? null : svg)
        })

        _this.saveDiagram(function (err, xml) {
          _this.setEncoded(downloadLink, 'diagram.bpmn', err ? null : xml)
        })
      })

      this.createNewDiagram()
    }
  }
</script>
<style lang="scss">
  /**/
  @import '~bpmn-js/dist/assets/diagram-js.css';
  @import '~bpmn-js/dist/assets/bpmn-font/css/bpmn.css';
  @import '~bpmn-js/dist/assets/bpmn-font/css/bpmn-codes.css';
  @import '~bpmn-js/dist/assets/bpmn-font/css/bpmn-embedded.css';
  /**/
  @import '~bpmn-js-properties-panel/dist/assets/bpmn-js-properties-panel.css';

  .containers {
    position: absolute;
    background-color: #ffffff;
    width: 100%;
    height: 110%;

    .canvas {
      width: 100%;
      height: 100%;
    }

    .panel{
      position: absolute;
      right: 0;
      top: 0;
      width: 300px;
    }

    .buttons{
      position: absolute;
      left: 20px;
      bottom: 20px;
      &>li{
          display:inline-block;margin: 5px;
        &>a{
            color: #999;
            background: #eee;
            cursor: not-allowed;
            padding: 8px;
            border: 1px solid #ccc;
          &.active{
            color: #333;
            background: #fff;
            cursor: pointer;
          }
        }
      }
    }

    .import{
      display: inline-block;
      margin:5px;
      li{
        list-style: none;
        a{
          color: #333;
          padding: 8px;
          border: 1px solid #ccc;
        }
      }
    }

    .bjs-powered-by {
      display: none;
    }
  }
</style>